body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background:#222222}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.wrapper-song-birds{width:1000px;margin:auto}.wrapper-result-score{background:#303030;text-align:center;margin:10px 0;padding:8px;border-radius:4px}.result-score-message{color:white;font-weight:bold;font-size:large}.result-score-win-message{color:white;font-weight:900;font-size:xx-large}.result-score-button{background:cadetblue;margin:10px 0;padding:8px;border-radius:4px;border:none;cursor:pointer;color:white;font-weight:bold;font-size:large}.result-score-button:hover{color:#2c4145}audio::-webkit-media-controls-panel{background-color:cadetblue}audio::-webkit-media-controls-play-button{-webkit-appearance:media-play-button;display:flex;flex:none;border:none;box-sizing:border-box;width:30px;height:30px;line-height:30px;margin-left:9px;margin-right:9px;padding:0;border-radius:50%;background-color:white}audio::-webkit-media-controls-current-time-display,audio::-webkit-media-controls-time-remaining-display{color:white;text-shadow:none}

.header_score,.header_type-birds{display:flex;justify-content:space-between}.header_score{color:white;font-size:xx-large;font-weight:bold}.type-birds-buttons{text-decoration:none;color:white;font-weight:bold;font-size:large}.type-birds-buttons:hover{color:#2c4145}.header_type-birds{background:cadetblue;margin:10px 0;padding:8px;border-radius:4px}.active-link{color:#2c4145}

.main-top{background:#303030;display:flex;margin:10px 0;padding:8px;border-radius:4px}.main-center{display:flex}.main-center__left{width:40%;background:#303030;margin:10px 0;padding:8px;border-radius:4px;height:100%}.main-center__right{width:calc(60% - 10px);margin-left:10px;background:#303030;margin:10px 0 10px 10px;padding:8px;border-radius:4px;color:white;font-weight:bold;font-size:large}.wrapper-top-image{width:200px;overflow:hidden;height:155px;display:flex}.top-image,.current-click-image{display:block;border-radius:8px;width:100%;height:100%}.top-player{display:block}.top-details{width:calc(100% - 300px);margin:auto;font-size:xx-large;font-weight:900;color:white}.left-bird{padding:10px;background:cadetblue;cursor:pointer;color:white;font-weight:bold;border-bottom:1px solid white}.left-bird:hover{background:#2c4145}.left-bird:last-child{border:none}.top-player{width:100%;height:40px;display:flex}.next-level{width:100%;background:#303030;margin:10px 0;padding:8px;border-radius:4px;border:none;cursor:pointer;font-weight:bold;font-size:large;color:grey}.next-level:hover{color:#2c4145}.bird-info-head{display:flex}.current-click-info{width:calc(100% - 300px);margin:auto}.about-bird{font-weight:normal;font-weight:initial;font-size:medium;font-size:initial}

.main_true-bird__1Os6q{background-color:darkolivegreen;pointer-events:none}.main_false-bird__V-xqk{background-color:darkred;pointer-events:none}.main_is-next__1Pqlw{background:cadetblue;color:white}

